import {mapMutations, mapState} from "vuex";

export default {
  computed: {
    ...mapState([
      'showList',
      'winning',
    ]),
  },
  methods: {
    ...mapMutations([
      'updateLastWins',
      'clearWinning',
    ]),
  },
  watch: {
    showList(value) {
      if (!value) {
        this.updateLastWins(Object.assign({}, this.winning));
        this.clearWinning();
      }
    },
  },
};