<template>
  <div class="block-blue bet-editor">
    <div class="columner">
      <button class="columner__column columner__column--2 bet-editor__column-item button-rb button-rb--red"
              @click="makeBet('red')"
              :disabled="isBetMade || isRoundEnded">

        <span class="button-rb__label">Put on<br class="button-rb__br"> red</span>
      </button>

      <input type="text"
             v-model="amount"
             class="columner__column columner__column--2 bet-editor__column-item bet-editor__input"/>

      <button class="columner__column columner__column--2 bet-editor__column-item button-rb button-rb--blue"
              @click="makeBet('blue')"
              :disabled="isBetMade || isRoundEnded">

        <span class="button-rb__label">Put on<br class="button-rb__br"> blue</span>
      </button>
    </div>

    <div class="bet-editor__text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</div>
  </div>
</template>

<script>
import {mapGetters, mapMutations, mapState} from "vuex";

export default {
  name: "BetEditor",
  data: () => ({
    amount: 100,
  }),
  computed: {
    ...mapState([
      'isRoundEnded',
    ]),
    ...mapGetters([
      'currentUser',
      'isBetMade',
    ]),
  },
  methods: {
    ...mapMutations([
      'updateBets',
      'decreaseCurrentUserAmount',
    ]),
    makeBet(color) {
      this.decreaseCurrentUserAmount(+this.amount);

      this.updateBets({
        color,
        user: this.currentUser,
        amount: +this.amount,
      });
    },
  },
}
</script>
