import Vue from 'vue';
import Identicon from "identicon.js";

const eventBus = new Vue();

export default eventBus;



export function generateAvatar() {
    const set = '123456789abcdefghijkmnpqrstvuwxyzABCDEFGHIJKLMNPQRSTUVWXYZ';
    let str = 'T';

    for (let i = 1; i < 34; i++) {
        str += set[Math.floor(Math.random() * set.length)];
    }

    // return str;

    const opts = {
        size: 5 * 11,
        format: 'svg',
        margin: 0,
        background: [0, 0, 0, 0],
        brightness: .3,
        saturation: 1
    };

    let result = '';

    for (let i = 1; i < str.length; i++) {
        result += str.charCodeAt(i).toString(16);
    }

    str = result;
    const startString = 'data:image/svg+xml;utf8,';
    let val = new Identicon(str, opts).toString(true);
    val = val.replace(/ stroke:.+?\d;/, "");

    return startString + val;
}
