<template>
  <div class="last-wins">
    <div class="last-wins__header">Last wins</div>

    <transition-group tag="div" name="anim-last-wins" class="last-wins__list">
      <div class="last-wins__list-item"
           :class="css(item.color)"
           v-for="item in lastWins"
           :key="item.index">

        {{ item.amount }} TRX
      </div>
    </transition-group>
  </div>
</template>

<script>
import {mapState} from "vuex";

export default {
  name: "LastWins",
  computed: {
    ...mapState([
      'lastWins',
    ]),
  },
  methods: {
    css(color) {
      return 'last-wins__list-item--' + color;
    },
  },
}
</script>
