<template>
  <div class="wheel">
    <div class="wheel__cursor-container">
      <transition name="anim-wheel-cursor">
        <svg class="wheel__cursor" width="51" height="24" viewBox="0 0 51 24" v-if="isRoundEnded && !winning.color">
          <path d="M25 24C25 10 14 0 0 0h51C37 0 26 10 26 24h-1z"/>
        </svg>
      </transition>
    </div>

    <div class="wheel__timer-container">
      <transition name="anim-wheel-timer" mode="out-in" @after-enter="startRound">
        <div class="wheel__timer" v-if="!isRoundEnded" key="timer">
          {{ animTimer.toFixed(1) }}s
        </div>

        <div class="wheel__timer" v-else-if="winning.color" key="winner">
          {{ winning.color.toUpperCase() }} WON<br>
          {{ winning.amount }} TRX
        </div>
      </transition>
    </div>

    <div class="wheel__wheel-container">
      <svg width="100%" height="100%" viewBox="0 0 330 330" :style="rotateStyleObj" @transitionend="determineWinner">
        <mask id="redblue-wheel-mask">
          <circle class="wheel__mask" r="155" cx="50%" cy="50%" fill="none" stroke="white"/>
        </mask>

        <g mask="url(#redblue-wheel-mask)">
          <circle class="wheel__blue" r="50%" cx="50%" cy="50%"/>
          <path class="wheel__red" :d="arc"/>
        </g>
      </svg>
    </div>

    <transition name="anim-wheel-glow">
      <div class="wheel__glow" v-if="winning.color" :class="{'wheel__glow--blue': winning.color === 'blue'}">
        <div class="wheel__glow-inner"></div>
      </div>
    </transition>
  </div>
</template>

<script>
import {mapActions, mapGetters, mapMutations, mapState} from 'vuex';
import {TweenLite} from "gsap/TweenLite";

export default {
  name: "Wheel",
  data: () => ({
    r: 165,
    animAngleArc: 0,

    timer: 5,
    animTimer: 0,

    rotateStyleObj: {'transition': 'none', 'transform': 'none'},
    angleRotation: 0,

    timerId: 0,
    red: false,
    blue: false,
  }),
  computed: {
    ...mapState([
      'isRoundEnded',
      'winning',
    ]),
    ...mapGetters([
      'totalRedPercentage',
      'isRoundCanBeStarted',
    ]),
    angleArc() {
      return this.totalRedPercentage * 359.99999 / 100;
    },
    arc() {
      const {r, x, y} = this;
      const fillFlag = this.animAngleArc <= 180 ? 0 : 1;
      return `M${r},0 A${r},${r} 0 ${fillFlag},0 ${x},${y} L${r},${r} Z`;
    },
    x() {
      return this.calcCoord('sin');
    },
    y() {
      return this.calcCoord('cos');
    }
  },
  methods: {
    ...mapMutations([
      'updateRoundEndedStatus',
    ]),
    ...mapActions([
      'setWinningColor',
      'clearRound',
    ]),
    calcCoord(func) {
      return this.r - Math[func](this.animAngleArc * Math.PI / 180) * this.r;
    },
    rotate() {
      const minMultiplier = 5;
      const maxMultiplier = 7;
      const rotationMultiplier = Math.floor(minMultiplier + Math.random() * (maxMultiplier - minMultiplier));

      const minAngle = 0;
      const maxAngle = 360;
      this.angleRotation = Math.floor(minAngle + Math.random() * (maxAngle - minAngle));

      const angle = this.angleRotation + rotationMultiplier * 360;
      this.rotateStyleObj.transition = 'transform 5s';

      this.$nextTick(() => {
        this.rotateStyleObj.transform = `rotate(${angle}deg)`;
      });
    },
    launchTimer() {
      if (!this.isRoundEnded) {
        TweenLite.fromTo(this.$data, this.timer, {animTimer: this.timer}, {
          animTimer: 0,
          ease: Power0.easeNone,
          onComplete: this.timerCallback,
        });
      }
    },
    timerCallback() {
      clearTimeout(this.timerId);
      this.updateRoundEndedStatus(true);
      this.rotate();
    },
    determineWinner() {
      const winningColor = this.angleRotation <= this.angleArc ? 'red' : 'blue';
      this.setWinningColor(winningColor);

      setTimeout(() => {
        this.clearRound();
        this.startRound();
      }, 3000);
    },
    startRound() {
      this.rotateStyleObj.transition = 'none';
      this.rotateStyleObj.transform = `rotate(${this.angleRotation}deg)`;
      TweenLite.fromTo(this.$data, 0, {animTimer: this.timer}, {animTimer: this.timer, ease: Power0.easeNone});
    },
  },
  watch: {
    angleArc(value) {
      TweenLite.to(this.$data, .5, {animAngleArc: value, ease: Power2.easeInOut});
    },
    isRoundCanBeStarted(value) {
      if (value) this.launchTimer();
    },
  },
  mounted() {
    this.startRound();
  }
}
</script>
