<template>
  <header class="header">
    <div class="header__amount">{{ Math.floor(amountAnim) }} TRX</div>

    <div class="header__user" v-if="currentUser">
      <img class="header__avatar avatar avatar-6 avatar-6--identicon" :src="currentUser.avatar">
      {{ currentUser.userName }}
    </div>
  </header>
</template>

<script>
import {mapGetters, mapState} from "vuex";
import {TweenLite} from "gsap/TweenLite";

export default {
  name: "Header",
  data: () => ({
    amountAnim: 0,
  }),
  computed: {
    ...mapState([
      'currentUserAmount',
    ]),
    ...mapGetters([
      'currentUser',
    ]),
  },
  watch: {
    currentUserAmount(value) {
      TweenLite.to(this.$data, .4, {amountAnim: value, ease: Power2.easeInOut});
    },
  },
}
</script>
