import {mapMutations, mapState} from "vuex";

export default {
  data: () => ({
    userIndexesShuffled: [],
    timerId: 0,
  }),
  computed: {
    ...mapState([
      'users',
      'isRoundEnded',
      'currentUserIndex',
    ]),
    userIndexes() {
      return this.users.map((user, index) => index).filter(index => index !== this.currentUserIndex);
    },
  },
  methods: {
    ...mapMutations([
      'updateBets',
    ]),
    shuffle(arr) {
      return [].concat(arr).sort(() => Math.random() - .5);
    },
    initUserIndexes() {
      this.userIndexesShuffled = this.shuffle(this.userIndexes);
    },
    pushUser() {
      if (this.userIndexesShuffled.length) {
        const minDuration = 100;
        const maxDuration = 2000;
        const timer = Math.floor(minDuration + Math.random() * (maxDuration - minDuration));

        this.timerId = setTimeout(() => {
          const user = this.users[this.userIndexesShuffled.pop()];
          const rnd = Math.random();

          this.updateBets({
            color: Math.floor(rnd * 2) ? 'red' : 'blue',
            user,
            amount: Math.floor(rnd * 300),
          });

          this.pushUser();
        }, timer);
      }
    },
  },
  watch: {
    isRoundEnded(value) {
      if (value) {
        clearTimeout(this.timerId);
        this.userIndexesShuffled = [];
      } else {
        this.initUserIndexes();
        this.pushUser();
      }
    },
    users(value) {
      if (value.length) {
        this.initUserIndexes();
        this.pushUser();
      }
    },
  },
};