import Vue from 'vue';
import Vuex from 'vuex';
import {generateAvatar} from "@/logics";

Vue.use(Vuex);


const lastWinsCount = 10;

export default new Vuex.Store({
  state: {
    showList: true,
    isRoundEnded: false,

    percentage: 0,

    lastWins: [],
    userNames: [
      'Dominator666',
      'RabbitEater3000',
      'RealGuy228',
      'DedMoroz',
      'LuckyMan777',
      'BadGuy13',
      'CreepyKitty',
      'DenzelWashington',
      'TomatoAss',
      'AngryBunny',
    ],
    users: [],
    currentUserIndex: 0,
    currentUserAmount: 0,
    bets: {
      red: [],
      blue: [],
    },
    winning: {
      color: '',
      amount: 0,
    },
  },
  getters: {
    currentUser(state) {
      // if (!state.users.length) return null;
      return state.users[state.currentUserIndex];
    },
    totalRed(state) {
      return state.bets['red'].reduce((sum, current) => sum + current.amount, 0);
    },
    totalBlue(state) {
      return state.bets['blue'].reduce((sum, current) => sum + current.amount, 0);
    },
    totalRedPercentage(state, getters) {
      return Math.floor(getters.totalRed / getters.total * 100) || 0;
    },
    totalBluePercentage(state, getters) {
      return !getters.totalRed && !getters.totalBlue ? 0 : 100 - getters.totalRedPercentage;
    },
    total(state, getters) {
      return getters.totalRed + getters.totalBlue;
    },
    isBetMade(state) {
      return Object.keys(state.bets).some(color => state.bets[color].some(bet => bet.user.index === state.currentUserIndex));
    },
    isRoundCanBeStarted(state) {
      return Object.keys(state.bets).every(color => state.bets[color].length);
    },
  },
  mutations: {
    updateHideList(state, payload) {
      state.showList = payload;
    },
    initUsers(state) {
      state.users = state.userNames.map((item, index) => ({
        userName: item,
        avatar: generateAvatar(),
        index,
      }));

      state.currentUserIndex = Math.floor(Math.random() * state.userNames.length);
      state.currentUserAmount = Math.floor(Math.random() * 2000);
    },
    updateCurrentUserAmount(state, payload) {
      state.currentUserAmount = payload;
    },
    decreaseCurrentUserAmount(state, payload) {
      state.currentUserAmount -= payload;
    },
    increaseCurrentUserAmount(state, payload) {
      state.currentUserAmount += payload;
    },
    updateRoundEndedStatus(state, payload) {
      state.isRoundEnded = payload;
    },
    updateLastWins(state, payload) {
      payload.index = Math.random();
      state.lastWins.pop();
      state.lastWins.unshift(payload);
    },
    initLastWins(state) {
      state.lastWins = [...Array(lastWinsCount)].map(() => {
        const rnd = Math.random();

        return {
          amount: Math.floor(rnd * 1000),
          color: Math.floor(rnd * 2) ? 'red' : 'blue',
          index: rnd,
        }
      });
    },
    updateBets(state, {color, user, amount}) {
      state.bets[color].push({user, amount, key: Math.random()});
    },
    clearBets(state) {
      Object.keys(state.bets).forEach(color => state.bets[color] = []);
    },
    updateWinning(state, {color, amount}) {
      state.winning.color = color;
      state.winning.amount = amount;
    },
    clearWinning(state) {
      state.winning.color = '';
      state.winning.amount = 0;
    },
  },
  actions: {
    init({commit}) {
      commit('initUsers');
      commit('initLastWins');
    },
    setWinningColor({commit, getters, state}, color) {
      const winning = {
        color,
        amount: getters.total,
      };

      commit('updateWinning', winning);
      // commit('updateLastWins', winning);


      const bets = state.bets[color];
      const foundCurrentUserBet = bets.find(bet => bet.user.index === state.currentUserIndex);

      if (foundCurrentUserBet) {
        const wonAmount = Math.floor(foundCurrentUserBet.amount / bets.reduce((sum, current) => sum + current.amount, 0) * getters.total);
        commit('increaseCurrentUserAmount', wonAmount);
      }
    },
    clearRound({commit}) {
      commit('updateRoundEndedStatus', false);
      commit('updateHideList', false);
      // commit('clearWinning');
      commit('clearBets');
    },
  },
})
