<template>
  <div>
    <Header/>

    <div class="red-blue__wrapper">
      <transition name="anim-overlay">
        <div class="redblue-overlay" v-if="winning.color"></div>
      </transition>

      <div class="columner">
        <div class="columner__column columner__column--1 column-game">
          <LastWins class="column-game__column-last-wins"/>

          <div class="column-game__column-wheel">
            <Wheel/>
          </div>
        </div>

        <div class="columner__column columner__column--1">
          <AtStake/>
          <BetEditor/>
        </div>
      </div>

      <div class="columner columner--margin">
        <div class="columner__column columner__column--1">
          <Bets color="red" :total="totalRed"/>
        </div>

        <div class="columner__column columner__column--1">
          <Bets color="blue" :total="totalBlue"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState, mapActions, mapGetters} from 'vuex';

import Wheel from "@/components/Wheel";
import AtStake from "@/components/AtStake";
import BetEditor from "@/components/BetEditor";
import Bets from "@/components/Bets";
import LastWins from "@/components/LastWins";
import Header from "@/components/Header";

import addBotsToBets from "@/components/mixins/addBotsToBets";
import updateBetHistoryAndClearWinner from "@/components/mixins/updateBetHistoryAndClearWinner";

export default {
  name: 'app',
  components: {
    Header,
    Wheel,
    AtStake,
    BetEditor,
    Bets,
    LastWins,
  },
  mixins: [
    addBotsToBets,
    updateBetHistoryAndClearWinner,
  ],
  computed: {
    ...mapState([
      'winning',
    ]),
    ...mapGetters([
      'totalBlue',
      'totalRed',
    ]),
  },
  methods: {
    ...mapActions([
      'init',
    ]),
  },
  mounted() {
    this.init();
  },
}
</script>

<style lang="scss">
@import "assets/styles/main";
</style>
