<template>
  <div class="block-blue at-stake">
    <div class="at-stake__column at-stake__side">
      <div class="at-stake__header at-stake__header--left">{{ bets['red'].length }}/{{ users.length }}</div>
      <div class="at-stake__content at-stake__content--left at-stake__content--red">{{ Math.floor(redAnim) }}%</div>
    </div>

    <div class="at-stake__column">
      <div class="at-stake__header">At Stake</div>
      <div class="at-stake__content">{{ Math.floor(atStakeAnim) }} TRX</div>
    </div>

    <div class="at-stake__column at-stake__side">
      <div class="at-stake__header at-stake__header--right">{{ bets['blue'].length }}/{{ users.length }}</div>
      <div class="at-stake__content at-stake__content--right at-stake__content--blue">{{ Math.floor(blueAnim) }}%</div>
    </div>
  </div>
</template>

<script>
import {TweenLite} from "gsap/TweenLite";
import {mapGetters, mapState} from "vuex";

export default {
  name: "AtStake",
  data: () => ({
    redAnim: 0,
    blueAnim: 0,
    atStakeAnim: 0,
  }),
  computed: {
    ...mapState([
      'bets',
      'users',
    ]),
    ...mapGetters([
      'total',
      'totalRedPercentage',
      'totalBluePercentage',
    ]),
  },
  watch: {
    totalRedPercentage(value) {
      TweenLite.to(this.$data, .4, {redAnim: value, ease: Power2.easeInOut});
    },
    totalBluePercentage(value) {
      TweenLite.to(this.$data, .4, {blueAnim: value, ease: Power2.easeInOut});
    },
    total(value) {
      TweenLite.to(this.$data, .4, {atStakeAnim: value, ease: Power2.easeInOut});
    }
  },
}
</script>
