<template>
  <div class="bets" :class="{'bets--fade': isRoundEnded}">
    <div class="bets__header">
      <div class="bets__header-users" :class="css.user">{{ localBets.length }}/{{ users.length }}</div>

      <div class="bets__header-bets">
        <span class="bets__header-label">Bets:</span>
        <span class="bets__header-amount">{{ total }} TRX</span>
      </div>
    </div>

    <transition name="anim-bet-list" @after-leave="endAnimHandler">
      <transition-group tag="div" name="anim-bet-item" class="bets__list" :class="css.list" v-if="showList">
        <div class="bets__list-item" :class="isCurrentUser(bet.user.index)" v-for="bet in localBets" :key="bet.key">
          <div class="bets__list-user">
            <img class="bets__avatar avatar avatar-5 avatar-5--identicon" :src="bet.user.avatar">
            {{ bet.user.userName }}
          </div>

          <div class="bets__item-amount">{{ bet.amount }} TRX</div>
        </div>

        <div class="bets__list-bottom" :class="css.bottom" key="bottom"></div>
      </transition-group>
    </transition>
  </div>
</template>

<script>
import {mapState, mapMutations} from "vuex";

export default {
  name: "Bets",
  props: {
    color: String,
    total: Number,
  },
  computed: {
    ...mapState([
      'showList',
      'isRoundEnded',
      'users',
      'currentUserIndex',
      'bets',
    ]),
    localBets() {
      return this.bets[this.color];
    },
    css() {
      return {
        list: 'bets__list--' + this.color,
        item: 'bets__list-item--' + this.color,
        bottom: 'bets__list-bottom--' + this.color,
        user: 'bets__header-users--' + this.color
      }
    },
  },
  methods: {
    ...mapMutations([
      'updateHideList',
    ]),
    isCurrentUser(index) {
      if (this.currentUserIndex === index)
        return this.css.item;
    },
    endAnimHandler() {
      this.updateHideList(true);
    },
  },
}
</script>
